import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GenerateReportDataPage } from './generate-report-data.page';


const routes: Routes = [
  {path: '', component: GenerateReportDataPage, pathMatch: 'full'},
  {path: 'edit/100/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet100/sheet100.module')
  .then( m => m.Sheet100Module)},
  {path: 'edit/250/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet250/sheet250.module')
  .then( m => m.Sheet250Module)},
  {path: 'edit/300/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet300/sheet300.module')
  .then( m => m.Sheet300Module)},
  {path: 'edit/302/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet302/sheet302.module')
  .then( m => m.Sheet302Module)},
  {path: 'edit/423/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet423/sheet423.module')
  .then( m => m.Sheet423Module)},
  {path: 'edit/453/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet453/sheet453.module')
  .then( m => m.Sheet453Module)},
  {path: 'edit/533/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet533/sheet533.module')
  .then( m => m.Sheet533Module)},
  {path: 'edit/600/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet600/sheet600.module')
  .then( m => m.Sheet600Module)},
  {path: 'edit/700/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet700/sheet700.module')
  .then( m => m.Sheet700Module)},
  {path: 'edit/700/:sheetName', loadChildren: () => import('./generate-report-edit/quarterly-special-reports/qdfir700/qdfir700.module')
  .then( m => m.Qdfir700Module)},
  {path: 'edit/800/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet800/sheet800.module')
  .then( m => m.Sheet800Module)},
  {path: 'edit/1000/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet1000/sheet1000.module')
  .then( m => m.Sheet1000Module)},
  {path: 'edit/1200/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheet1200/sheet1200.module')
  .then( m => m.Sheet1200Module)},
  {path: 'edit/321/:sheetName', loadChildren: () => import('./generate-report-edit/normal-reports/sheet321/sheet321.module')
  .then(m => m.Sheet321Module)},
  {path: 'edit/mstdr2/:sheetName', loadChildren: () => import('./generate-report-edit/special-report-edit/sheetmstdr2/sheetmstdr2.module')
  .then( m => m.Sheetmstdr2Module)},
  {path: 'edit/:sheetName', loadChildren: () => import('./generate-report-edit/generate-report-edit.module')
  .then( m => m.GenerateReportEditModule)},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GenerateReportDataRoutingModule { }
